import { useEffect, useState } from "react"

// import { useScenarioEditor } from "./ScenarioEditorContext"

const getLabel = (obj) => !!obj.abbrLabel ? obj.abbrLabel : obj.label
// const escapeQuotes = (txt) => txt.replace(/"/g, '\\"')
const escapeQuotes = (text) => text

const RadioGroup = ({name, options, getter, setter}) => {

    const changeHandler = (event) => {
        const {value} = event.target
        setter(value)
    }

    return (
        <ul style={{
                marginLeft: "5px",
                marginBottom: "10px",
                listStyle: "none",
            }}
        >
            {options.map((option, i) =>
            <li key={i}>
                <label>
                    <input
                        type="radio"
                        name={escapeQuotes(name)}
                        value={option.value}
                        checked={option.value === getter()}
                        onChange={changeHandler}
                    />
                    &nbsp; {option.label}
                </label>
            </li>
            )}
        </ul>
    )
}

const IntvVariantPrompt = ({intv, variant, prompt, stateGetter, stateSetter}) => {

    const getValue = () => stateGetter(intv.id, variant, prompt.id)
    const setValue = (value) => stateSetter(intv.id, variant, prompt.id, value)

    return (
        <div style={{marginLeft: "10px"}}>
            <i>{prompt.label}</i>
            <RadioGroup
                name={`${intv.id}.${variant}.${prompt.id}`}
                options={
                    prompt.answers.map(ans => {
                        return {
                            value: ans.id,
                            label: getLabel(ans)
                        }
                    })
                }
                getter={getValue}
                setter={setValue}
            />
        </div>
    )
}

const IntvVariantPrompts = ({intvID, variantJSON, intvLabel, variantDesc, contexts, intvGetter, promptValueGetter, promptValueSetter}) => {
    // const {scenario, scenarioType} = useScenarioEditor()
    const [intv, setIntv] = useState(null)

    useEffect(
        () => {
            if (!intv) {
                setIntv(intvGetter(intvID))
            }
        }, [intvID, intv, intvGetter]
    )
    if (!intv) {
        return (<div>calculating</div>)
    }
    return (
        <div>
            <div>
                <b>Intervention: </b> {intvLabel}
                {"" === variantDesc ? " " : `: ${variantDesc} `}
                <b> Used By: </b> {contexts}
            </div>
            <ul>
            {intv.prompts.map(prompt =>
                <li key={prompt.id}>
                    <IntvVariantPrompt
                        intv={intv}
                        variant={variantJSON}
                        prompt={prompt}
                        stateGetter={promptValueGetter}
                        stateSetter={promptValueSetter}
                    />
                </li>
            )}
            </ul>

        </div>
    )

}


export default IntvVariantPrompts